import VueSlider from "vue-slider-component";
import 'vue-slider-component/theme/default.css'

export default {
    name: "core",

    components: {
        VueSlider
    },

    data() {
        return {
            value: 0,

            options: {
                dotSize: 20,
                duration: 0.2,
                dragOnClick: true,
            },

            videoTime: 0,
            videoTimeRemaining: 0
        }
    },

    mounted() {
        document.title = "yoranus.com";

        const video = document.getElementById("video");
        const promise = video.play();

        if (promise !== undefined) {
            promise.then(() => {
                this.value = 15;
            }).catch(() => {
                video.muted = true;
                video.play();
                this.value = 0;
            });
        }

        const interval = setInterval(() => {
            const state = video.readyState;
    
            if (state === 4) {
                clearInterval(interval);

                this.videoTime = format(video.currentTime.toFixed(0));
                this.videoTimeRemaining = format(video.duration.toFixed(0));
    
                setInterval(() => {
                    this.videoTime = format(video.currentTime.toFixed(0));
                    this.videoTimeRemaining = format(video.duration.toFixed(0));
                }, 100);
    
                return;
            }
        });
    },

    watch: {
        value: (val) => {
            document.getElementById("video").volume = val / 100;
        }
    }
};

function format(time) {   
    const hrs = ~~(time / 3600);
    const mins = ~~((time % 3600) / 60);
    const secs = ~~time % 60;

    let ret = "";

    if (hrs > 0) {
        ret += "" + hrs + ":" + (mins < 10 ? "0" : "");
    }

    ret += "" + mins + ":" + (secs < 10 ? "0" : "");
    ret += "" + secs;
    
    return ret;
}